@import url(https://fonts.googleapis.com/css?family=Noto+Sans|Muli);
body {
  font-family: "Noto Sans", sans-serif;
  font-size: 14px;
  line-height: 1.42857143;
  color: #6a707a;
  background-color: #fff; }

.navigation {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  display: block;
  padding: 25px 40px;
  text-align: left; }

.brand-logo {
  margin-top: 4px;
  display: inline-block; }
  .brand-logo img {
    display: inline-block;
    height: 14px;
    vertical-align: middle; }

.background {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: auto;
  text-align: center;
  background: linear-gradient(to bottom, #1c77c3 60px, #1c77c3 20%, #5da9e8); }

.container {
  width: 100%;
  height: 100%;
  text-align: center;
  animation: fade-down 1s ease-out; }
  .container:before {
    display: inline-block;
    height: 100%;
    vertical-align: middle;
    content: " "; }

.wrapper {
  display: inline-block;
  width: 460px;
  padding: 56px 0;
  vertical-align: middle; }

.box {
  display: block;
  padding: 0px;
  margin: 25px 0 20px;
  background: #fff;
  border-radius: 3px;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.2); }
  .box .intro {
    padding: 50px 50px 0;
    margin-bottom: 0;
    text-align: center; }
    .box .intro h1 {
      font-family: "Muli", sans-serif;
      font-size: 24px;
      font-weight: 500;
      margin: 0 0 40px 0;
      color: #6a707a; }
  .box .list {
    display: block;
    max-height: 500px;
    overflow: auto;
    border-top: 1px solid #e3e4e7; }
    .box .list .entry {
      position: relative;
      display: block;
      padding: 15px 25px;
      text-align: left;
      background: transparent;
      border-bottom: 1px solid #e3e4e7;
      transition: background 0.3s linear; }
      .box .list .entry:last-child {
        border-bottom: 0; }
      .box .list .entry a {
        font-family: "Noto Sans", sans-serif;
        color: #474b51;
        text-decoration: none;
        font-size: 16px; }

